<template>
  <b-container fluid v-if="!$lodash.isEmpty(show)">
    <b-row class="mt-5">
      <b-col cols="12" lg="3">
        <b-img-lazy :src="imageLink" alt="show.name"></b-img-lazy>
      </b-col>
      <b-col cols="12" lg="6">
        <h5>{{ show.name }}</h5>
        <p class="text-left" v-html="show.summary"></p>
        <hr />

        <div
          class="badge bg-secondary mx-1 p-2"
          v-for="(genre, i) in show.genres"
          :key="i"
        >
          <span class="text-white">{{ genre }}</span>
        </div>
      </b-col>
      <b-col cols="12" lg="3">
        <ul class="list-unstyled text-left">
          <li>rating: {{ show.rating.average }}</li>
          <li>network: {{ show.network.name }}</li>
          <li>country: {{ show.network.country.name }}</li>
          <li>premiered: {{ show.premiered }}</li>
        </ul>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
export default {
  data: () => ({
    show: {},
  }),
  computed: {
    imageLink() {
      console.log(Object.getOwnPropertyDescriptor(this.show, "image"));
      return Object.getOwnPropertyDescriptor(this.show, "image")
        ? this.show.image["medium"] || this.show.image["original"]
        : "#";
    },
  },
  mounted() {
    this.$store.dispatch("getShow", this.$route.params.id).then((r) => {
      this.show = r;
    });
  },
};
</script>

